* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.main {
  place-content: center;
  place-self: center;
  gap: 46px 48px;
  padding-top: 50px;
  padding-left: 40px;
  padding-right: 40px;
  display: grid;
}

@media (width >= 1px) {
  .main {
    grid-template-columns: repeat(1, auto);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, auto);
  }
}

ul {
  margin: 0;
  padding: 0;
}

.header {
  --color: #00acdc;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.nav__list {
  flex-wrap: nowrap;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.img {
  width: 40px;
  height: 40px;
}

.is-active {
  color: var(--color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 10px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  flex-flow: column;
  justify-content: center;
  place-self: center;
  align-items: center;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
  transition: transform .3s;
  display: flex;
}

.card__pictures {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__name {
  width: 100%;
  margin: 0;
}

.card__name-name {
  text-align: left;
  color: #060b35;
  margin: 36px 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name-code {
  text-align: left;
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.card__review-rewiews {
  text-align: right;
  align-self: end;
  width: 100%;
  padding-left: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-let {
  text-align: left;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-num {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
}

.card__button-text {
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button:hover .card__button-text {
  color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name-nameme {
  color: #34568b;
}

.stars {
  width: 112px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
